import React from 'react';
import bridge from '@vkontakte/vk-bridge';
import Papa from 'papaparse';

//import { animated, useSpring } from 'react-spring'

import { FixedSizeList as List } from "react-window";
import axios from 'axios-jsonp-pro'

import io from 'socket.io-client';
import Header from '../components/MainEpicHeader'
import Banner from '../components/Banner'
import { PanelHeader, PanelHeaderBack, View, Panel, Snackbar, Avatar, ConfigProvider, SimpleCell, ScreenSpinner, Alert, Input, ModalCard, ModalRoot } from '@vkontakte/vkui';
import Icon16Done from '@vkontakte/icons/dist/16/done';
import Icon16Cancel from '@vkontakte/icons/dist/16/cancel';

const images = { AppleMusic: require('../img/AppleMusic.svg'), Spotify: require('../img/Spotify.svg'), Yandex: require('../img/Yandex.svg'), Error: require('../img/error.png') }
const socket = io('https://music.dan0102dan.ru');

export default class Home extends React.Component {
	constructor(props) {
		super(props);
	
		this.state = {
		  activePanel: 'home',
		  history: ['home'],
		  snackbar: null,
		  vk_platform: 'browser',
		  songs: [],
		  count: 0,
		  link: ''
		}
	  }

	  goBack = () => {
		var history = this.state.history;

		if (this.state.activePanel === 'home') {
			bridge.send('VKWebAppDisableSwipeBack');
		}
		
		if ( history.length > 1 ) {
		  history.pop()
		  if (history[history.length - 1] === 'home') {
			this.setState({songs: [], count: 0})
			this.props.setTabbar(true)
			document.documentElement.style.setProperty('--padding', 'calc(var(--safe-area-inset-bottom) + var(--tabbar_height))');
			document.documentElement.style.setProperty('--bottom', 'calc(var(--tabbar_height) + var(--safe-area-inset-bottom))');
		  }
		  this.setState({ activePanel: history[history.length - 1] })
		}
	  }
	  goToPage( name ) {
		window.history.pushState( { panel: name }, name );
		if (this.state.activePanel === 'home') {
			bridge.send('VKWebAppEnableSwipeBack');
			this.props.setTabbar(null)
			document.documentElement.style.setProperty('--padding', '0');
			document.documentElement.style.setProperty('--bottom', 'var(--safe-area-inset-bottom)');
		}
		this.setState({ 
			activePanel: name,
			history: [...this.state.history, name]
		})
	  }

	  openSuccess () {
		this.setState({ snackbar:
		  <Snackbar
			layout="vertical"
			onClose={() => this.setState({ snackbar: null })}
			before={<Avatar size={24} style={{ backgroundColor: 'var(--accent)' }}><Icon16Done fill="#fff" width={14} height={14} /></Avatar>}
		  >
			Отправили!
		  </Snackbar>
		});
	  }
	  openError (description) {
		this.setState({ snackbar:
		  <Snackbar
			layout="vertical"
			onClose={() => this.setState({ snackbar: null })}
			before={<Avatar size={24} style={{ backgroundImage: 'linear-gradient(135deg, #fc7373, #cb5e5a)' }}><Icon16Cancel fill="#fff" width={14} height={14} /></Avatar>}
		  >
			{description}
		  </Snackbar>
		});
	  }
	  openCaptcha (playlist, key, socket, captcha_sid, service, captcha_img) {
		this.props.setModal(
		  <ModalRoot activeModal={'Captcha'} onClose={this.props.setModal(null)}>
		   <ModalCard
			id={'Captcha'}
			onClose={() => this.props.setModal(null)}
			header="Введите капчу"
			actions={[
				{
				title: 'Отправить',
				mode: 'primary',
				action: () => this.returnCaptcha(playlist, key, socket, captcha_sid, service)
				}
			]}
			>
			  <img alt={'error'} style={{margin: 'auto', display: 'block', width: '60%', borderRadius: 8, marginTop: 10, marginBottom: 10}} src={captcha_img} />
			  <Input defaultValue='' onChange={(e)=> key=e.target.value} onKeyDown={(e) => {if (e.key === 'Enter') this.returnCaptcha(playlist, key, socket, captcha_sid, service)}} />
		   </ModalCard>
		  </ModalRoot>);
	  }

	  componentDidMount() {
		window.addEventListener('popstate', () => this.goBack());
		if (window.location.search) {
			var vk_platform = window.location.search.match(/vk_platform=([a-z_]+)/)
			this.setState({vk_platform: vk_platform[1].toString()})
		}
	  }

	  returnCaptcha(playlist, key, socket, captcha_sid, service) {
		socket.emit(service, {playlist: playlist, sign: window.location.search.slice(1), captcha: {key, captcha_sid}});
		this.props.setModal(null);
	}

render () {
	return (
	<Panel id={this.id}>
	 <ConfigProvider
	 scheme={document.body.getAttribute('scheme') ? document.body.getAttribute('scheme') : 'bright_light'}
	 >
	  <View activePanel={this.state.activePanel} history={this.state.history} onSwipeBack={this.goBack}>
	 	<Panel id="home">
          <Header>Главная</Header>
		  <Banner
		  	id='Apple'
		  	mode="image"
		  	background={
				<div
				  id='BannerBackground'
				  style={{
					backgroundImage: 'linear-gradient(326deg, #9b2952 0%, #171428 74%)'
				  }}
				/>
			  }
			before={<Avatar style={{background: 'none'}} size={86} mode="image" src={images.AppleMusic} />}
            header="Apple Music"
			subheader="Перенести музыку из файла Apple."
			onClick={() => document.getElementById('AppleInput').click()}
          >
			<input
      		  style={{display: 'none'}}
			  type="file"
			  accept='.txt'	
      		  id="AppleInput"
      		  onChange={(e)=>{
				this.props.setPopout(<ScreenSpinner />);
				let reader = new FileReader();
				var text, playlist = [];

				reader.readAsText(e.target.files[0], 'UTF-8');
				reader.onload = (evt) => {
					text = evt.target.result;

					let parsed = Papa.parse(text, {
						header: true
					  });
					if (parsed.errors.length && !parsed.data.length) {
						this.openError('Ошибка');
						return;
					}
					parsed.data.pop();
					for (let index = 0; parsed.data.length > index; index++) {
						const
						  track = parsed.data[index],
						  keys = Object.keys(track);
						if (!keys.includes('Name') || !keys.includes('Artist') || !keys.includes('Album')) {
							this.openError('Ошибка');
							return;
						}
						playlist.push({
						  index,
						  name           : track.Name,
						  artist         : track.Artist,
						  album          : track.Album
						});
					  }
				this.setState({count: playlist.length}, () => this.goToPage('transfer'));

			   try {
				this.props.setPopout(null);
				  socket.emit('AppleMusic', {playlist: playlist, sign: window.location.search.slice(1)});
				  socket.on('message', (data) => {
					console.log(data)
					if (data?.error) {
						if (data.error === 'Captcha needed') {
						  playlist = playlist.splice(playlist.findIndex(e => e.name === data.description.request_params[data.description.request_params.findIndex(e => e.key === 'q')].value))
						  var key = '';

						  this.openCaptcha(playlist, key, socket, data.description.captcha_sid, 'AppleMusic', data.description.captcha_img);
						}
						else this.setState({songs: 'error'});
					  }
					  else {
						 var songs = this.state.songs;
						 songs.unshift(data);
						 this.setState({songs});
						}
				  });
				} catch (e) {
					this.setState({songs: 'error'});
				}
			  }
	  		  }}
	   		/>
		  </Banner>
		  <Banner
		  	id='Spotify'
		  	mode="image"
		  	background={
				<div
				  id='BannerBackground'
				  style={{
					backgroundImage: 'linear-gradient(326deg, #000000 0%, #313131 74%)'
				  }}
				/>
			  }
            before={<Avatar style={{background: 'none'}} size={86} mode="image" src={images.Spotify} />}
            header="Spotify"
			subheader="Перенести музыку из плейлиста Spotify."
			onClick={()=>
				this.props.setPopout(
				<Alert
				  actions={[{
					title: 'Отмена',
					autoclose: true,
					mode: 'cancel'
				  }, {
					title: 'Далее',
					autoclose: true,
					action: async () => {
					this.props.setPopout(<ScreenSpinner />);
					try {
						var user; var playlistId;
						if (this.state.link.search(/playlist/i) > -1) {
						  var str = this.state.link.split('playlist');
						  playlistId = Number(str[1].replace(/[^\d]/g, ''));
						  str.splice(1, 1); str = str[0];
						  str = str.split('users');
						  user = str[1].replace(/\//g, '');
						} else {
						  this.props.setPopout(null);
						  this.openError('Неверная ссылка');
						  return;
						}
					socket.emit('YandexMusic', {user, playlistId, sign: window.location.search.slice(1)});
				  	socket.on('message', (data) => {
						this.props.setPopout(null);
						console.log(data)
						if (data?.error) {
						   this.setState({songs: 'error'});
						}
						else if (data?.count) {
							this.setState({count: data.count}, () => this.goToPage('transfer'));
						}
						else {
						   var songs = this.state.songs;
						   songs.unshift(data);
						   this.setState({songs});
			  			}
				  	});
					} catch {
						this.props.setPopout(null);
						this.openError('Ошибка');
					}
				   }
				  }]}
				  onClose={()=>this.props.setPopout(null)}
				>
				  <h2>Введите ссылку</h2>
				  <p>Должна быть ссылка на публичный плейлист</p>
				  <Input style={{marginTop: 16, marginBottom: -7}} onChange={(e)=> { this.setState({ link: e.target.value }) }} type="text" />
				</Alert>
			  )}
          />

		  <Banner
		  	id='Yandex'
		  	mode="image"
		  	background={
				<div
				  id='BannerBackground'
				  style={{
					backgroundImage: 'linear-gradient(326deg, #29539B 0%, #1E3B70 74%)'
				  }}
				/>
			  }
			before={<Avatar style={{background: 'none'}} size={86} mode="image" src={images.Yandex} />}
            header="Яндекс Музыка"
			subheader="Перенести музыку из плейлиста Яндекс Музыки."
			onClick={()=>
				this.props.setPopout(
				<Alert
				  actions={[{
					title: 'Отмена',
					autoclose: true,
					mode: 'cancel'
				  }, {
					title: 'Далее',
					autoclose: true,
					action: async () => {
					this.props.setPopout(<ScreenSpinner />);
					try {
						var user; var playlistId;
						if (this.state.link.search(/playlist/i) > -1) {
						  var str = this.state.link.split('playlist');
						  playlistId = Number(str[1].replace(/[^\d]/g, ''));
						  str.splice(1, 1); str = str[0];
						  str = str.split('users');
						  user = str[1].replace(/\//g, '');
						} else {
						  this.props.setPopout(null);
						  this.openError('Неверная ссылка');
						  return;
						}
						var playlist = [];

				var trackIds = await axios.get(`https://cors-anywhere.herokuapp.com/https://music.yandex.ru/handlers/playlist.jsx?owner=${user}&kinds=${playlistId}&light=true&madeFor=&withLikesCount=true&lang=ru&external-domain=music.yandex.ru&overembed=false`);
				trackIds = trackIds.data.playlist.trackIds.toString();
				await axios.post(`https://cors-anywhere.herokuapp.com/https://music.yandex.ru/handlers/track-entries.jsx`, {entries: trackIds})
					.then(async function (response) {
							  for (var value of response.data) {
								 try {
								  playlist.push({
									  name           : value.title,
									  artist         : value.artists[0].name,
									  album          : value.albums[0].title
								  });
								  } catch {
									  continue;
								  }
							  }
							});
						this.setState({count: playlist.length}, () => this.goToPage('transfer'));

					socket.emit('YandexMusic', {playlist: playlist, sign: window.location.search.slice(1)});
				  	socket.on('message', (data) => {
						console.log(data)
						this.props.setPopout(null);
						
						if (data?.error) {
						  if (data.error === 'Captcha needed') {
							playlist = playlist.splice(playlist.findIndex(e => e.name === data.description.request_params[data.description.request_params.findIndex(e => e.key === 'q')].value))
							var key = '';
							this.openCaptcha(playlist, key, socket, data.description.captcha_sid, 'YandexMusic', data.description.captcha_img);
						  }
						  else this.setState({songs: 'error'});
						}
						else {
						   var songs = this.state.songs;
						   songs.unshift(data);
						   this.setState({songs});
			  			}
				  	});
					} catch {
						this.props.setPopout(null);
						this.openError('Ошибка');
					}
				   }
				  }]}
				  onClose={()=>this.props.setPopout(null)}
				>
				  <h2>Введите ссылку</h2>
				  <p>Должна быть ссылка на публичный плейлист</p>
				  <Input style={{marginTop: 16, marginBottom: -7}} onChange={(e)=> { this.setState({ link: e.target.value }) }} type="text" />
				</Alert>
			  )}
          />
	  	{this.state.snackbar}
      </Panel>

	  <Panel id="transfer">
          <PanelHeader
			left={<PanelHeaderBack style={{cursor: 'pointer'}}
			  onClick={()=>this.goBack()} 
			/>}
		  >
			{this.state.songs === 'error' ? 'Ошибка!' : `Найдено ${this.state.songs.length}/${this.state.count}` }
		  </PanelHeader>
      		{this.state.songs === 'error'
			? <div>
				<img alt={'error'} style={{display: 'flex', marginLeft: 'auto', marginRight: 'auto', width: '80vw', height: '80vh', objectFit: 'contain'}} src={images.Error}/>
			</div>
			: <List
        	  className="List"
			  height={window.innerHeight-60}
			  width={window.innerWidth}
			  itemCount={this.state.songs.length}
			  itemSize={60}
      		>
        	 {({ index, style }) => (
			<div style={style}>
			  <SimpleCell
			  	onClick={async()=>{
					console.log(1)
				  }}
			  	className={'SongCell'}
			 	key={this.state.songs[index].id}
			 	before={<Avatar mode="image" src={this.state.songs[index].image} />}
			 	description={this.state.songs[index].artist}
				after={Math.floor(this.state.songs[index].duration/60)+':'+`${this.state.songs[index].duration%60}`.padEnd(2, '0')}>
			  	{this.state.songs[index].title}
			  </SimpleCell>
			</div>
	  		)}
      		</List>}
	  	{this.state.snackbar}
      </Panel>
      </View>
	 </ConfigProvider>
	</Panel>
	)
 }
}